import React, { Component } from 'react';
import { connect } from 'react-redux';
import { 
    ChatboxWrapper, 
    ChatboxTitle, 
    ChatboxList, 
    ChatboxItem, 
    ChatboxBtm, 
    ChatboxInput, 
    Button 
} from '../style';

class Chatbox extends Component {
    render() {
        const { list } =this.props;
        return (
            <ChatboxWrapper>
                <ChatboxTitle>Chat Bot</ChatboxTitle>
                {
                    list.map((item) => {
                        return (                            
                                <ChatboxList key={item.get('id')}>
                                    <ChatboxItem className={item.get('type')}>
                                        {item.get('text')}
                                    </ChatboxItem>
                                </ChatboxList> 
                        );
                    })
                }
                <ChatboxBtm>
                    <ChatboxInput />
                    <Button className='enter'>Enter</Button>
                </ChatboxBtm>
            </ChatboxWrapper>            
        )
    }
}

const mapState = (state) => ({
    list: state.getIn(['home', 'messageList'])
})

export default connect(mapState, null)(Chatbox);